let url = process.env.REACT_APP_LOCAL_URL
if (!url) url = "https://thereweather.space"
const socket = io.connect(url)

export default function Messenger() {
    // const dispatch = useDispatch()
    const [prevmsg, setprevmsg] = useState([])
    const { userInfo } = useSelector((state) => state.itemReducer)
    //      (onChange)-hoon
    const [msgevent, setmsgevent] = useState("")
    //  -hoon
    const [newMsgSection, setNewMsgSection] = useState([])
    //   -hoon
    const [receiver_id, setreceiver_id] = useState("")
    const [curRoom, setcurRoom] = useState("") //-hoon
    //       -hoon
    const [joinedRoom, setjoinedRoom] = useState([""])
    const [roomInOut, setRoomInOut] = useState(false)

    const [recievemessage, setrecievemessage] = useState([])
    console.log(recievemessage)
    //   ,     (onChange)-hoon
    function roomNamefunc(e) {
        console.log(e.target.value)
        setreceiver_id(e.target.value)
    }
    //   
    function roomNameSubmit() {
        //   sort   .-hoon
        const user_id_sort = [userInfo.user_id, receiver_id].sort()
        // socket.emit("enter_room", `${user_id_sort[0]}_${user_id_sort[1]}`)
        // setcurRoom(`${user_id_sort[0]}_${user_id_sort[1]}`)

        //  axios      -hoon
        axios({
            url: url + "/chat/rooms",
            method: "post",
            headers: {
                "Content-Type": "application/json",
            },
            data: {
                user_id: userInfo.user_id,
                receiver_id: receiver_id,
                roomName: `${user_id_sort[0]}_${user_id_sort[1]}`,
            },
            withCredentials: true,
        }).then((res) => {
            const joinRoom = new Set(res.data)
            setjoinedRoom([...joinRoom])
        })
    }
    //      -hoon
    useEffect(() => {
        axios({
            url: url + `/chat/rooms?user_id=${userInfo.user_id}`,
            method: "get",
            headers: {
                "Content-Type": "application/json",
            },
            withCredentials: true,
        }).then((res) => {
            const joinRoom = new Set(res.data)
            setjoinedRoom([...joinRoom])
        })
    }, [])
    ///////////// /////
    useEffect(() => {
        axios({
            url: url + `/chat/messagelist`,
            method: "post",
            headers: {
                "Content-Type": "application/json",
            },
            data: {
                user_id: userInfo.user_id,
                receiver_id: curRoom
                    .replace("_", "")
                    .replace(`${userInfo.user_id}`, ""),
                roomName: curRoom,
            },
            withCredentials: true,
        }).then((res) => {
            setprevmsg(res.data)
        })
        socket.on("sendmsg", (msgobj) =>
            setNewMsgSection([...newMsgSection, msgobj])
        )
    }, [])

    useState(() => {
        setNewMsgSection([...newMsgSection])
        setprevmsg([...prevmsg])
        console.log(newMsgSection)
    }, [roomInOut, prevmsg, newMsgSection])

    //     -hoon
    function roomListClick(clickRoomName) {
        setRoomInOut(true)
        // -hoon
        console.log(clickRoomName)
        setcurRoom(clickRoomName)
        // socket.emit("enter_room", clickRoomName)
        //    .
        axios({
            url: url + `/chat/messagelist`,
            method: "post",
            headers: {
                "Content-Type": "application/json",
            },
            data: {
                user_id: userInfo.user_id,
                receiver_id: curRoom
                    .replace("_", "")
                    .replace(`${userInfo.user_id}`, ""),
                roomName: curRoom,
            },
            withCredentials: true,
        }).then((res) => {
            setprevmsg(res.data)
        })
    }
    //    
    function msgfunc(e) {
        console.log(e.target.value)
        setmsgevent(e.target.value)
    }
    //    
    function msgSubmit() {
        axios({
            url: url + `/chat/messagelist`,
            method: "put",
            headers: {
                "Content-Type": "application/json",
            },
            data: {
                user_id: userInfo.user_id,
                receiver_id: curRoom
                    .replace("_", "")
                    .replace(`${userInfo.user_id}`, ""),
                roomName: curRoom,
                chatcontent: msgevent,
            },
            withCredentials: true,
        }).then((res) =>
            socket.emit("message", {
                user_id: userInfo.user_id,
                receiver_id: curRoom
                    .replace("_", "")
                    .replace(`${userInfo.user_id}`, ""),
                chatcontent: msgevent,
            })
        )
        setmsgevent("")
    }

    // socket.on("newMsg", (msg2) => {
    //     // console.log(" ")
    //     //   
    //     setprevmsg([...msg, msg2])
    // })
    // useEffect(() => {
    //     axios({
    //         url: url + "/chat/messagelist",
    //         method: "post",
    //         headers: {
    //             "Content-Type": "application/json",
    //         },
    //         data: {
    //             roomName: curRoom,
    //             chatcontent: JSON.stringify(msg),
    //         },
    //         withCredentials: true,
    //     }).then((res) => {})
    //     // .then((res) => console.log("  "))
    // }, [msg])

    const goBackHandler = () => {
        setRoomInOut(false)
    }

    const MeDiv = styled.div`
        // border-bottom: 1px solid green;
        // margin-bottom: 1rem;
        // margin-top: 1rem;
        // padding-bottom: 0.5rem;
        // vertical-align: center;
        // align-self: center;
        text-align: right;

        overflow: auto;
    `
    const YouDiv = styled.div`
        // border-bottom: 1px solid black;
        // margin-bottom: 1rem;
        // margin-top: 1rem;
        // padding-bottom: 0.5rem;
        // vertical-align: center;
        // align-self: center;
        text-align: left;

        overflow: auto;
    `
    return (
        <Container className="mapcontainer">
            {!roomInOut ? (
                <RoomChatDiv>
                    <ProfileName>{userInfo.user_id}</ProfileName>
                    <UserPhotoDiv>
                        <UserPhotoImg src={userInfo.user_Photo} />
                    </UserPhotoDiv>
                    <FriendInputAndBtn>
                        <FriendInput
                            onChange={(e) => roomNamefunc(e)}
                            placeholder=" ID"
                            required
                            type="text"
                        />
                        <Buttons>
                            <Button>
                                <FontAwesomeIcon
                                    onClick={roomNameSubmit}
                                    icon={faPlusSquare}
                                />
                            </Button>
                        </Buttons>
                    </FriendInputAndBtn>
                    <RoomList>
                        <FriendListDiv>{" "}</FriendListDiv>
                        {joinedRoom.map((el) => (
                            <FriendListDiv onClick={() => roomListClick(el)}>
                                {el
                                    .replace("_", "")
                                    .replace(`${userInfo.user_id}`, "")}
                            </FriendListDiv>
                        ))}
                    </RoomList>
                </RoomChatDiv>
            ) : (
                <>
                    <GoBackButton>
                        <FontAwesomeIcon
                            icon={faChevronLeft}
                            className="fa-fw"
                            color="#ACB5BD"
                            onClick={goBackHandler}
                        />
                    </GoBackButton>
                    <ChatList>
                        <div>{""}</div>
                        {prevmsg.map((el) => {
                            if (userInfo.user_id === el.user_id) {
                                return <MeDiv>{el.chatcontent}</MeDiv>
                            } else {
                                return <YouDiv>{el.chatcontent}</YouDiv>
                            }
                        })}
                        {newMsgSection.map((el) => {
                            if (userInfo.user_id === el.user_id) {
                                return <MeDiv>{el.chatcontent}</MeDiv>
                            } else {
                                return <YouDiv>{el.chatcontent}</YouDiv>
                            }
                        })}
                    </ChatList>
                    <input
                        onChange={(e) => msgfunc(e)}
                        placeholder=""
                        required
                        type="text"
                        value={msgevent}
                    />
                    <Buttons>
                        <Button>
                            <FontAwesomeIcon
                                onClick={msgSubmit}
                                icon={faPaperPlane}
                            />
                        </Button>
                    </Buttons>
                </>
            )}
        </Container>
    )
}
