const url = process.env.REACT_APP_LOCAL_URL

export default function Home() {
    // 
    const dispatch = useDispatch()
    const { item, postInfo } = useSelector((state) => state.itemReducer)
    console.log(item)
    // const { userInfo } = useSelector((state) => state.itemReducer)
    // dispatch(changeUser(axiosData))

    const [weatherData, setWeatherData] = useState()
    if (weatherData) {
        console.log(weatherData)
        console.log(weatherData.item)
    }

    useEffect(() => {
        if (navigator.geolocation) {
            // GeoLocation    
            navigator.geolocation.getCurrentPosition(function (position) {
                let lat = position.coords.latitude, // 
                    lon = position.coords.longitude // 
                console.log(lat, lon) // 
                axios({
                    url: url + "/map",
                    method: "post",
                    data: { lat: lat, lon: lon },
                    withCredentials: true,
                }).then((res) => {
                    //console.log(res.data)
                    //console.log(res.data.item)
                    setWeatherData(res.data)
                    dispatch(updateWeatherInfo(res.data))
                })
            })
        }
    }, [])

    //  (, ,   ..)
    const [currentPosts, setcurrentPosts] = useState([])
    useEffect(() => {
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(function (position) {
                let lat = position.coords.latitude, // 
                    lon = position.coords.longitude // 

                // ,  
                let lat2 = Math.floor(lat)
                let lon2 = Math.floor(lon)
                console.log("  : ", lat2, lon2)

                axios({
                    url: url + "/home",
                    method: "post",
                    data: {
                        lat2: lat2,
                        lon2: lon2,
                    },
                    withCredentials: true,
                }).then((res) => {
                    console.log("  : ", res.data)
                    setcurrentPosts(res.data)
                    // dispatch(homePost(res.data))
                })
            })
        }
    }, [])

    // 
    const [todaysDate, settodaysDate] = useState("")
    useEffect(() => {
        let date = new Date()
        const formatDate = (currentDate) => {
            let formatted = `${currentDate.getFullYear()} ${
                currentDate.getMonth() + 1
            } ${currentDate.getDate()}`
            return formatted
        }
        settodaysDate(formatDate(date))
    })

    //  
    let [currentTemp, setcurrentTemp] = useState("")
    let [currentWind, setcurrentWind] = useState("")
    let [currentWeather, setcurrentWeather] = useState("")
    // 
    useEffect(() => {
        let tempArr = []
        for (let i = 0; i < currentPosts.length; i++) {
            tempArr.push(currentPosts[i].temp) // temp 
        }
        //    
        function getCount(arr) {
            return arr.reduce((pv, cv) => {
                pv[cv] = (pv[cv] || 0) + 1
                return pv
            }, {})
        }

        let obj = getCount(tempArr) // {hot: 6, cold: 3}
        // 
        let arr = Object.values(obj) // [6, 3]
        let maxNum = Math.max(...arr) // 6
        let maxTemp
        //   key 
        for (let key in obj) {
            if (obj[key] === maxNum) {
                maxTemp = key
            }
        }
        setcurrentTemp(maxTemp)
    })

    // 
    useEffect(() => {
        let windArr = []
        for (let i = 0; i < currentPosts.length; i++) {
            windArr.push(currentPosts[i].wind)
        }
        //    
        function getCount(arr) {
            return arr.reduce((pv, cv) => {
                pv[cv] = (pv[cv] || 0) + 1
                return pv
            }, {})
        }

        let obj = getCount(windArr)
        // 
        let arr = Object.values(obj)
        let maxNum = Math.max(...arr)
        let maxWind
        //   key 
        for (let key in obj) {
            if (obj[key] === maxNum) {
                maxWind = key
            }
        }
        setcurrentWind(maxWind)
    })

    // 
    useEffect(() => {
        let weatherArr = []
        for (let i = 0; i < currentPosts.length; i++) {
            weatherArr.push(currentPosts[i].weather)
        }
        //    
        function getCount(arr) {
            return arr.reduce((pv, cv) => {
                pv[cv] = (pv[cv] || 0) + 1
                return pv
            }, {})
        }

        let obj = getCount(weatherArr)
        // 
        let arr = Object.values(obj)
        let maxNum = Math.max(...arr)
        let maxWeather
        //   key 
        for (let key in obj) {
            if (obj[key] === maxNum) {
                maxWeather = key
            }
        }
        setcurrentWeather(maxWeather)
    })

    return (
        <div className="homecontainer">
            {/* <Loading /> */}
            <TodaysDate>: {todaysDate}</TodaysDate>
            <HomeContainer>
                <LeftContainer1>
                    <LeftNav1>
                        <p>{"00"}</p>
                        <div className="weatherInfo">
                            <ul>
                                {/* <li>: {todaysDate}</li> */}
                                <li> : {currentTemp}</li>
                                <li> : {currentWind}</li>
                                <li> : {currentWeather}</li>
                            </ul>
                        </div>
                    </LeftNav1>
                    <LeftNav2>
                        <p> </p>
                        <div className="weatherInfo">
                            <ul>
                                {/* {console.log(weatherData.item)}  */}
                                {/* weatherData -> {item: Array(30)}, weatherData.item -> [ baseDate: '20211106',baseTime: '2130',category: 'T1H', fcstDate: '20211107', fcstTime: '0300', fcstValue: '10', nx: 59, ny: 128, ... ] */}
                                {/* { weatherData && weatherData.item.map((info, idx) => { return <li kye={idx}>:{info.baseDate}</li> })[0] } */}
                                {weatherData &&
                                    weatherData.item.map((info, idx) => {
                                        return (
                                            <li kye={idx}>
                                                 :{info.baseTime}
                                            </li>
                                        )
                                    })[0]}
                                {weatherData &&
                                    weatherData.item.map((info, idx) => {
                                        return (
                                            <li kye={idx}>
                                                 :{info.fcstValue}
                                            </li>
                                        )
                                    })[24]}{" "}
                                {/* T1H */}
                                {weatherData &&
                                    weatherData.item.map((info, idx) => {
                                        return (
                                            <li kye={idx}>
                                                 :
                                                {info.fcstValue < "9"
                                                    ? "   "
                                                    : info.fcstValue < "14"
                                                    ? " "
                                                    : "  "}
                                            </li>
                                        )
                                    })[54]}{" "}
                                {/* WSD */}
                                {weatherData &&
                                    weatherData.item.map((info, idx) => {
                                        return (
                                            <li kye={idx}>
                                                 :
                                                {info.fcstValue === "0"
                                                    ? ""
                                                    : info.fcstValue === "1"
                                                    ? ""
                                                    : info.fcstValue === "3"
                                                    ? ""
                                                    : info.fcstValue === "5"
                                                    ? ""
                                                    : ""}
                                            </li>
                                        )
                                    })[6]}{" "}
                                {/* PTY */}
                            </ul>
                        </div>
                    </LeftNav2>
                    <LeftNav3>
                        <p>{"00 "}   </p>
                        <div className="codiInfo">
                            <Codi
                                src={`${process.env.PUBLIC_URL}img/codi/.png`}
                                alt=""
                            ></Codi>
                            <Codi
                                src={`${process.env.PUBLIC_URL}img/codi/.png`}
                                alt=""
                            ></Codi>
                            <Codi
                                src={`${process.env.PUBLIC_URL}img/codi/.png`}
                                alt=""
                            ></Codi>
                        </div>
                    </LeftNav3>
                </LeftContainer1>

                <RightContainer>
                    <RightNav1>
                        <span id="location">{"00"} </span>
                    </RightNav1>
                    {currentPosts.map((el) => (
                        <div className="userPost" key={el.id}>
                            <img src={el.post_photo} />
                        </div>
                    ))}
                </RightContainer>
            </HomeContainer>
        </div>
    )
}
